import Vue from 'vue'
import axios from '@/axios';
import Index from './Index.vue'
import Vant from 'vant';
import 'vant/lib/index.css';
import vueCookies from 'vue-cookies'

Vue.use(vueCookies);
Vue.use(Vant);

new Vue({
  render: h => h(Index),
	axios
}).$mount('#app')



