
<script>
	import axios from 'axios';
	import "@babel/polyfill";
	const raptorBrokerApiServer_ = "/raptorBrokerServer";
	const raptorCoreApiServer_ = "/raptorCoreServer";
	const raptorCustomereApiServer_ = "/raptorCustomerServer";
	//API url配置
	const apiUrl_ = {
		auth: {
		  login: raptorBrokerApiServer_ + "/service/auth/login.do",
		  logout: raptorBrokerApiServer_ + "/service/auth/logout.do",
		  queryBroker: raptorBrokerApiServer_ + "/service/auth/queryBroker.do",
		  verification: raptorBrokerApiServer_ + "/service/auth/verificationCode.do"
		}
	};

	export default {
		raptorBrokerApiServer_,
		raptorCoreApiServer_,
		apiUrl_,
		initDicMap: (dicTypes) => {
			return new Promise((resolve, reject) => {
				axios({
					method: 'post',
					headers: {
						'Content-Type': 'application/json'
					},
					url: raptorCoreApiServer_ + '/service/global/dic/dicCache.do',
					data: JSON.stringify({types : dicTypes})
				}).then((response) => {
					resolve(response);
				}).catch((error) => {
					reject(error);
				});
			});
		},
		 //获取单个系统参数 获取中介生成协议的二级域名
  getParam: value => {
    return new Promise((resolve, reject) => {
      axios({
        method: 'post',
        headers: {
          'Content-Type': 'application/json'
        },
        url: raptorCoreApiServer_ + '/service/global/dic/getSystemParameter.do',
        data: JSON.stringify({ code: value })
      })
        .then(response => {
          resolve(response)
        })
        .catch(error => {
          reject(error)
        })
    })
  },
		getUrlString: (name) => {
			var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
			var r = window.location.search.substr(1).match(reg);
			if(r != null) return unescape(r[2]);
			return null;
		},
		isVip: () => {
			let data = JSON.parse(sessionStorage.getItem("brokerSession"));
			if (data.company.isVip) {
				// console.log(now.toLocaleTimeString())
				//TODO 如果会员过期的情况   var now = new Date();
				return true;
			} else {
				return false;
			}
		}
	}
</script>



